<template>
	<div class="home" v-if="this.show">
		<Form @save="save" :newURL="'budget'">
			<div slot="title">
				{{ $t('title') }}
			</div>
			<div class="row" v-if="this.id">
				<div class="col-sm-6">
					<div class="form-group">
						<label for="id">{{ $t('id') }}</label>
						<input class="form-control" id="id" v-model="form.id" type="text" disabled />
					</div>
				</div>
				<div class="col-sm-3">
					<div class="form-group">
						<label for="createdAt">{{ $t('createdAt') }}</label>
						<input class="form-control" id="createdAt" v-model="form.createdAt" type="datetime-local" disabled />
					</div>
				</div>
				<div class="col-sm-3">
					<div class="form-group">
						<label for="updatedAt">{{ $t('updatedAt') }}</label>
						<input class="form-control" id="updatedAt" v-model="form.updatedAt" type="datetime-local" disabled />
					</div>
				</div>
			</div>
			<div class="row">
				<div class="col-sm-6">
					<div class="form-group mb-3" v-if="this.userType == 'seller'">
						<label for="seller_id">{{ $t('seller') }} : {{ this.form.seller_name }}</label>
					</div>
					<div class="form-group mb-3" v-else>
						<label for="seller_id">{{ $t('seller') }}</label>
						<div class="input-group mb-3">
							<select class="form-control custom-select" id="seller_id" v-model="form.seller_id">
								<!-- <option selected>{{ $t('choose') }}</option> -->
								<option :value="seller.id" v-for="(seller, i) in this.sellers" :key="i">
									{{ seller.name }}
								</option>
							</select>
						</div>
					</div>
				</div>
				<div class="col-sm-3">
					<div class="form-group">
						<label for="layout">{{ $t('layout') }}</label>
						<div class="input-group">
							<select class="form-control custom-select" id="layout" v-model="form.layout" @change="changeLayout">
								<!-- <option selected>{{ $t('choose') }}</option> -->
								<option :value="i" v-for="(layout, i) in this.layouts" :key="i">
									{{ layout.name }}
								</option>
							</select>
						</div>
					</div>
				</div>
				<div class="col-sm-3">
					<div class="form-group">
						<label for="expiration_date">{{ $t('expiration_date') }}</label>
						<input class="form-control" id="expiration_date" v-model="form.expiration_date" type="datetime-local" />
					</div>
				</div>
			</div>
			<div class="row">
				<div class="col-sm-12 pt-2">
					<Client :form="this.form" @changed="changedClient" />
				</div>
			</div>
			<div class="row">
				<div class="col-sm-12 pt-4">
					<Dimensions :form="this.form" />
				</div>
			</div>
			<div class="row">
				<div class="col-sm-12 pt-4">
					<Card class="card" v-if="this.form">
						<div class="card-header">
							{{ $t('equipments') }}
						</div>
						<div class="">
							<div class="row" v-if="this.form">
								<div class="col-sm-4 pb-4" v-for="(equipment, i) in this.form.equipments" :key="i">
									<div v-if="equipment">
										<Filters
											:index="equipment.index"
											:logist="logist"
											:form="form"
											:dimension="form.dimension"
											:tax="parseFloat(form.installment_tax)"
											@changed="changeEquipment"
											@delete="deleteEquipment(equipment.index)"
											v-if="equipment.type == 'filters'"
										/>
										<Engines
											:index="equipment.index"
											:form="form"
											:tax="parseFloat(form.installment_tax)"
											@changed="changeEquipment"
											@delete="deleteEquipment(equipment.index)"
											v-if="equipment.type == 'engines'"
										/>
										<Lids
											:index="equipment.index"
											:form="form"
											:tax="parseFloat(form.installment_tax)"
											@changed="changeEquipment"
											@delete="deleteEquipment(equipment.index)"
											v-if="equipment.type == 'lids'"
										/>
										<Sands
											:index="equipment.index"
											:form="form"
											:tax="parseFloat(form.installment_tax)"
											:sand_kg="totalSandFilter"
											@changed="changeEquipment"
											@delete="deleteEquipment(equipment.index)"
											v-if="equipment.type == 'sands'"
										/>
										<Blankets
											:index="equipment.index"
											:logist="logist"
											:form="form"
											:m2_facial="isNaN(form.m2_facial) ? 0 : parseFloat(form.m2_facial)"
											:tax="parseFloat(form.installment_tax)"
											@changed="changeEquipment"
											@delete="deleteEquipment(equipment.index)"
											v-if="equipment.type == 'blankets'"
										/>
										<Profiles
											:index="equipment.index"
											:logist="logist"
											:form="form"
											:perimeter="isNaN(form.perimeter) ? 0 : parseFloat(form.perimeter)"
											:tax="parseFloat(form.installment_tax)"
											@changed="changeEquipment"
											@delete="deleteEquipment(equipment.index)"
											v-if="equipment.type == 'profiles'"
										/>
										<Vinyls
											:index="equipment.index"
											:logist="logist"
											:form="form"
											:thickness="form.thickness"
											:m2_total="isNaN(form.m2_total) ? 0 : parseFloat(form.m2_total)"
											:tax="parseFloat(form.installment_tax)"
											@changed="changeEquipment"
											@delete="deleteEquipment(equipment.index)"
											v-if="equipment.type == 'vinyls'"
										/>
										<Supplies
											:index="equipment.index"
											:form="form"
											:m2_total="isNaN(form.m2_total) ? 0 : parseFloat(form.m2_total)"
											:tax="parseFloat(form.installment_tax)"
											@changed="changeEquipment"
											@delete="deleteEquipment(equipment.index)"
											v-if="equipment.type == 'supplies'"
										/>
									</div>
								</div>
							</div>
							<div class="row">
								<div class="col-sm-6"></div>
								<div class="col-sm-6 text-right">
									<button type="button" class="btn btn-primary small" @click="showEquipment">
										{{ $t('add_equipment') }}
									</button>
								</div>
							</div>
						</div>
					</Card>
				</div>
			</div>
			<div class="row">
				<div class="col-sm-12 pt-4">
					<ManPower :form="this.form" :layout="this.form.layout" :logist="this.logist" v-if="this.form" @changed="changedValues" />
				</div>
			</div>
			<div class="row">
				<div class="col-sm-12 pt-4">
					<div class="card" v-if="this.form">
						<div class="card-header">
							{{ $t('totals') }}
						</div>
						<div class="">
							<div class="row">
								<div class="col-sm-4">
									<div class="form-group mb-3">
										<label for="payment">{{ $t('payment') }}</label>
										<div class="input-group mb-3">
											<select class="form-control custom-select" id="payment" v-model="form.payment" @change="changeTax">
												<!-- <option selected>{{ $t('choose') }}</option> -->
												<option :value="i" v-for="(payment, i) in this.payments" :key="i">
													{{ payment }}
												</option>
											</select>
										</div>
									</div>
								</div>
								<div class="col-sm-2" v-if="this.form.payment != 'in_cash' && this.form.payment != 'debit_card'">
									<div class="form-group">
										<label for="installment_number">
											{{ $t('installment_number') }}
										</label>
										<input class="form-control" id="installment_number" v-model="form.installment_number" type="number" @keyup="changeTax" />
									</div>
								</div>
								<div class="col-sm-2">
									<div class="form-group">
										<label for="down_payment">{{ $t('down_payment') }}</label>
										<input class="form-control" id="down_payment" v-model="form.down_payment" type="number" @keyup="changeTax" />
									</div>
								</div>
								<div class="col-sm-2" v-if="this.form.payment != 'in_cash'">
									<div class="form-group">
										<label for="installment_tax">
											{{ $t('installment_tax') }}
										</label>
										<input class="form-control" id="installment_tax" v-model="form.installment_tax" type="number" disabled />
									</div>
								</div>
								<div class="col-sm-2" v-if="this.form.payment != 'in_cash' && this.form.payment != 'debit_card'">
									<div class="form-group">
										<label for="installment_value">
											{{ $t('installment_value') }}
										</label>
										<input class="form-control" id="installment_value" :value="(form.forward_price_total / form.installment_number).toFixed(2)" type="number" disabled />
									</div>
								</div>
								<div class="col-sm-2">
									<div class="form-group">
										<label for="art">{{ $t('art') }}</label>
										<input class="form-control" v-model="form.art" type="number" @keyup="changeTax" />
									</div>
								</div>
								<div class="col-sm-2">
									<div class="form-group">
										<label for="discount">{{ $t('discount') }}</label>
										<input class="form-control" v-model="form.discount" type="number" @keyup="changeTax" />
									</div>
								</div>
								<div class="col-sm-2">
									<div class="form-group">
										<label for="cash_price">{{ $t('cash_price') }}</label>
										<input class="form-control" id="cash_price" type="text" :value="this.form.cash_price" disabled />
									</div>
								</div>
								<div class="col-sm-2" v-if="this.form.payment != 'in_cash'">
									<div class="form-group">
										<label for="forward_price">{{ $t('forward_price') }}</label>
										<input class="form-control" id="forward_price" type="text" :value="this.form.forward_price" disabled />
									</div>
								</div>
								<div class="col-sm-2">
									<div class="form-group">
										<label for="cash_price_total">
											{{ $t('cash_price_total') }}
										</label>
										<input class="form-control" id="cash_price_total" type="text" :value="this.form.cash_price_total" disabled />
									</div>
								</div>
								<div class="col-sm-2" v-if="this.form.payment != 'in_cash'">
									<div class="form-group">
										<label for="forward_price_total">
											{{ $t('forward_price_total') }}
										</label>
										<input class="form-control" id="forward_price_total" type="text" :value="this.form.forward_price_total" disabled />
									</div>
								</div>
								<div class="col-sm-2">
									<div class="form-group">
										<label for="deadline_days">
											{{ $t('deadline_days') }}
										</label>
										<input class="form-control" id="deadline_days" type="text" v-model="form.deadline_days" />
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
			<button type="button" class="btn btn-primary small" @click="showPDF" v-if="this.id">
				{{ $t('see_pdf') }}
			</button>
			<button type="button" class="btn btn-primary small ml-2" @click="finishBudget" v-if="this.id">
				{{ $t('finish_budget') }}
			</button>
		</Form>

		<FloatCard :title="$t('add_equipment')" :show="this.showAddEquipment" @close="showAddEquipment = false">
			<div class="row">
				<div class="col-sm-12">
					<div class="form-group mb-3">
						<label for="new_equipment">{{ $t('equipment') }}</label>
						<div class="input-group mb-3">
							<select class="form-control custom-select" id="new_equipment" v-model="newEquipment">
								<!-- <option selected>{{ $t('choose') }}</option> -->
								<option :value="i" v-for="(equipment, i) in this.equipments" :key="i">
									{{ equipment }}
								</option>
							</select>
						</div>
					</div>
				</div>
			</div>
			<div slot="footer">
				<button type="button" class="btn btn-primary" data-dismiss="modal" @click="addEquipment">
					{{ $t('add_equipment') }}
				</button>
			</div>
		</FloatCard>
		<Alert :title="this.alert.title" :message="this.alert.message" @close="alert = {}" />
	</div>
</template>

<script>
	import Methods from '../../../../helpers/methods'
	import MessageError from '../../../../helpers/messages-errors'

	import Budgets from '../../../../controllers/budgets/budgets'
	import Logists from '../../../../controllers/persons/logists'
	import Clients from '../../../../controllers/persons/clients'
	import Sellers from '../../../../controllers/persons/sellers'
	// COMPONENTS
	import Form from '../../../components/Form/Form'
	import Alert from '../../../components/Alert/Alert'
	import Card from '../../../components/Card/Card'
	import FloatCard from '../../../components/FloatCard/FloatCard'
	// BUDGET COMPONENTS
	import Client from '../Client/Client.vue'
	import Dimensions from '../Dimenions/Dimension'
	import Filters from '../Equipments/Filters'
	import Engines from '../Equipments/Engines'
	import Lids from '../Equipments/Lids'
	import Sands from '../Equipments/Sands'
	import Blankets from '../Equipments/Blankets'
	import Profiles from '../Equipments/Profiles'
	import Vinyls from '../Equipments/Vinyls'
	import Supplies from '../Equipments/Supplies'
	import ManPower from '../ManPower/ManPower'

	import Layouts from '../data/layouts'
	import Status from '../data/status'
	import Payments from '../data/payments'
	import Equipments from '../data/equipments'
	import messages from './messages'
	import './style.css'

	export default {
		name: 'BudgetForm',
		props: { id: String },
		i18n: { messages },
		components: {
			Form,
			Alert,
			Card,
			FloatCard,
			Client,
			Dimensions,
			Filters,
			Engines,
			Lids,
			Sands,
			Blankets,
			Profiles,
			Vinyls,
			Supplies,
			ManPower,
		},
		data() {
			return {
				form: {
					id: null,
					client_id: null,
					logist_id: null,
					seller_id: null,
					status: null,
					layout: null,
					date: null,
					length: null,
					width: null,
					initial_depth: null,
					final_depth: null,
					average_depth: null,
					sidewalk_width: null,
					perimeter: null,
					pool_area: null,
				},
				clients: [],
				sellers: [],
				userType: localStorage.userType,
				logist: {},
				payments: Payments,
				equipments: Equipments,
				status: Status,
				layouts: Layouts,
				layout: '',
				alert: {},
				totalSandFilter: 0,
				newEquipment: 'filter',
				showEquipments: false,
				showAddEquipment: false,
				show: false,
			}
		},
		mounted() {
			this.load()
			this.reload()
		},
		watch: {
			id() {
				this.load()
				this.reload()
			},
		},
		methods: {
			reload() {
				this.show = false
				setTimeout(() => {
					this.show = true
				}, 100)
			},
			load() {
				this.form.clients = {}
				this.form.equipments = null
				if (this.id) {
					Budgets.getBudget(this.id, (result) => {
						if (result.data.status == 'finished') {
							this.$router.push('/budget-pdf/' + this.id)
						} else {
							this.form = Object.assign({}, result.data)
							if (result.data) {
								this.form.expiration_date = Methods.fixSequelizeOnlyDate(this.form.expiration_date)
								this.form.updatedAt = Methods.fixSequelizeDate(this.form.updatedAt)
								this.form.createdAt = Methods.fixSequelizeDate(this.form.createdAt)
								this.form.art = result.data.art
								const equipments = Object.assign({}, result.data.equipments)
								this.loadEquipments(equipments)
								this.changeTax()
							}
						}
					})
				} else {
					this.form = {}
					this.form.payment = Object.keys(this.payments)[0]
					this.form.status = Object.keys(this.status)[0]
					this.form.layout = Object.keys(this.layouts)[0]
					this.form.expiration_date = Methods.fixSequelizeDate(new Date().toISOString())
					this.form.cash_price = 0
					this.form.cash_price_total = 0
					this.form.down_payment = 0
					this.form.art = 0
					this.form.discount = 0
					this.form.deadline_days = 30
					this.form.beach = false

					this.form.m2_total = 0
					this.form.m3_total = 0
					this.form.perimeter = 0
					this.form.sidewalk_width = 0

					setTimeout(() => {
						this.changeLayout()
					}, 1)
				}
				if (this.userType == 'seller') {
					Sellers.getByToken((result) => {
						this.sellers = result.data
						this.logist = result.data.logists
						this.form.seller_id = result.data.id
						this.form.seller_name = result.data.name
					})
				} else {
					Logists.getByToken((result) => {
						this.logist = result.data
					})
					Sellers.list((result) => {
						this.sellers = result.data
					})
				}
				Clients.list((result) => {
					this.clients = result.data
				})
			},
			save() {
				if (this.id && this.id != 0) {
					Budgets.updateBudget(this.form, (result) => {
						this.alert = MessageError.getMessage(this, result, 'title', 'budgets')
					})
				} else {
					if (this.form) delete this.form.id
					Budgets.insertBudget(this.form, (result) => {
						if (result && result.data) {
							this.form.id = result.data.id
							window.location.hash = '/budget/' + result.data.id
							this.form.expiration_date = Methods.fixSequelizeDate(result.data.expiration_date)
							this.form.updatedAt = Methods.fixSequelizeDate(result.data.updatedAt)
							this.form.createdAt = Methods.fixSequelizeDate(result.data.createdAt)
							this.alert = MessageError.getMessage(this, result, 'title')
						} else {
							this.alert = MessageError.getMessage(this, { status: 'Error' }, 'title')
						}
					})
				}
			},
			changedClient() {},
			changeEquipment(equipment) {
				if (equipment && (equipment.engine || equipment.lid || equipment.sand)) {
					const index = this.form.equipments[equipment.index].index
					if (this.form.equipments[equipment.index].type == 'filters') {
						this.form.equipments[equipment.index].sand_kg = equipment.sand_kg
					}
					this.totalSandFilter = 0
					for (const i in this.form.equipments) {
						if (this.form.equipments[i].index > index && this.form.equipments[i].type == 'engines' && equipment.engine) {
							this.form.equipments[i].equipment_id = equipment.engine.equipment_id
							const engine = this.form.equipments[i]
							this.form.equipments[i] = null
							setTimeout(() => {
								this.form.equipments[i] = engine
							}, 100)
						} else if (this.form.equipments[i].index > index && this.form.equipments[i].type == 'lids' && equipment.lid) {
							this.form.equipments[i].equipment_id = equipment.lid.equipment_id
							const lid = this.form.equipments[i]
							this.form.equipments[i] = null
							setTimeout(() => {
								this.form.equipments[i] = lid
							}, 100)
						} else if (this.form.equipments[i].index > index && this.form.equipments[i].type == 'sands' && equipment.sand) {
							this.form.equipments[i].equipment_id = equipment.sand.equipment_id
							const sand = this.form.equipments[i]
							this.form.equipments[i] = null
							setTimeout(() => {
								this.form.equipments[i] = sand
							}, 100)
						} else if (this.form.equipments[i] && this.form.equipments[i].type == 'filters') {
							this.totalSandFilter += this.form.equipments[i].sand_kg ? parseInt(this.form.equipments[i].sand_kg) : 0
						}
					}
				}
				setTimeout(() => {
					this.changedValues()
				}, 100)
			},
			changedValues() {
				this.form.cash_price = 0
				this.form.forward_price = 0
				for (const i in this.form.equipments) {
					if (this.form.equipments[i]) {
						this.form.cash_price += Methods.fixNumber(this.form.equipments[i].final_price)
					}
				}

				const construction_labor = Methods.fixNumber(this.form.construction_labor) * (this.form.m2_total > 0 ? this.form.m2_total : 1)
				const excavation_labor = Methods.fixNumber(this.form.excavation_labor) * (this.form.m3_total > 0 ? this.form.m3_total : 1)
				const earth_removal_labor = Methods.fixNumber(this.form.earth_removal_labor) * (this.form.m3_total > 0 ? this.form.m3_total : 1) * 1.2
				const short_wall_labor = Methods.fixNumber(this.form.short_wall_labor)
				const short_wall_labor_m2 = Methods.fixNumber(this.form.short_wall_labor_m2)
				const total_short_wall = short_wall_labor * short_wall_labor_m2
				this.form.total_short_wall = total_short_wall
				let subfloor_labor = 0
				if (this.form.perimeter > 0 && this.form.sidewalk_width > 0) {
					subfloor_labor = Methods.fixNumber(this.form.subfloor_labor) * (this.form.perimeter * this.form.sidewalk_width + this.form.sidewalk_width * this.form.sidewalk_width) * 4
				} else {
					subfloor_labor = Methods.fixNumber(this.form.subfloor_labor)
				}
				const material_placement_labor = Methods.fixNumber(this.form.material_placement_labor)
				const reserve = Methods.fixNumber(this.form.reserve)
				const job_monitoring_fee = Methods.fixNumber(this.form.job_monitoring_fee)

				this.form.cash_price += construction_labor
				this.form.cash_price += excavation_labor
				this.form.cash_price += earth_removal_labor
				this.form.cash_price += total_short_wall
				this.form.cash_price += subfloor_labor
				this.form.cash_price += material_placement_labor
				this.form.cash_price += reserve
				this.form.cash_price += job_monitoring_fee

				this.form.cash_price_total = this.form.cash_price - Methods.fixNumber(this.form.discount) + this.form.cash_price * (Methods.fixNumber(this.form.art) / 100)

				this.form.cash_price = this.form.cash_price.toFixed(2)
				this.form.cash_price_total = this.form.cash_price_total.toFixed(2)
				this.form = Object.assign({}, this.form)
			},
			changeTax() {
				try {
					this.changedValues()
					if (this.form.payment == 'in_cash') {
						this.form.installment_tax = 0
						this.form.forward_price = Methods.fixNumber(this.form.cash_price).toFixed(2)
						this.form.forward_price_total = Methods.fixNumber(this.form.cash_price_total).toFixed(2)
					} else if (this.form.installment_number > 0) {
						if (this.form.payment == 'debit_card') {
							this.form.installment_tax = parseFloat(this.logist[this.form.payment])
							this.form.forward_price = (Methods.fixNumber(this.form.cash_price) + (Methods.fixNumber(this.form.cash_price) * Methods.fixNumber(this.form.installment_tax)) / 100).toFixed(2)
							this.form.forward_price_total = (Methods.fixNumber(this.form.cash_price_total) + (Methods.fixNumber(this.form.cash_price_total) * Methods.fixNumber(this.form.installment_tax)) / 100).toFixed(
								2
							)
						} else if (this.form.payment == 'by_financial' || this.form.payment == 'by_financial_down_payment') {
							const installment_tax = JSON.parse(this.logist[this.form.payment])
							const tax = Methods.fixNumber(installment_tax[this.form.installment_number - 1])
							if (tax) {
								this.form.installment_tax = 0
							} else {
								const installment_cash = (Methods.fixNumber(this.form.cash_price) * Methods.fixNumber(tax)).toFixed(2)
								const total_price_cash = installment_cash * this.form.installment_number
								this.form.installment_tax = ((total_price_cash * 100) / this.form.cash_price - 100).toFixed(2)
							}
						} else if (this.form.payment == 'installment_credit_card') {
							const installment_tax = JSON.parse(this.logist[this.form.payment])
							this.form.installment_tax = installment_tax[this.form.installment_number - 1]
						}
					} else {
						this.form.installment_tax = 0
					}
					const cash_price = Methods.fixNumber(this.form.cash_price)
					const down_payment = Methods.fixNumber(this.form.down_payment)

					const cash_price_total = Methods.fixNumber(this.form.cash_price_total)
					const installment_tax = Methods.fixNumber(this.form.installment_tax)

					this.form.cash_price_total = (cash_price_total - down_payment).toFixed(2)
					this.form.forward_price = (cash_price - down_payment + ((cash_price - down_payment) * installment_tax) / 100).toFixed(2)
					this.form.forward_price_total = (cash_price_total - down_payment + ((cash_price_total - down_payment) * installment_tax) / 100).toFixed(2)
				} catch (e) {
					this.form.installment_tax = 0
				}
			},
			loadEquipments(equipments) {
				this.form.equipments = {}
				for (const i in equipments) {
					const equipment = equipments[i]
					this.form.equipments[equipment.index] = equipment
					this.form.equipments[equipment.index].discount = Methods.fixNumber(equipment.discount)
					this.form.equipments[equipment.index].final_price = Methods.fixNumber(equipment.final_price)
				}
			},
			changeLayout() {
				this.layout = Layouts[this.form.layout]
				this.form.equipments = {}
				for (const i in this.layout.equipments) {
					const type = this.layout.equipments[i]
					let finded = false
					for (const j in this.form.equipments) {
						if (this.form.equipments[j] && this.form.equipments[j].type == type) {
							finded = true
						}
					}
					if (!finded) {
						const index = Object.keys(this.form.equipments).length
						this.form.equipments[index] = { type, index }
					}
				}
				this.changedValues()
			},
			showEquipment() {
				this.showAddEquipment = true
				this.newEquipment = Object.keys(this.equipments)[0]
			},
			addEquipment() {
				const lastIndex = Object.keys(this.form.equipments)[Object.keys(this.form.equipments).length - 1]
				const index = parseInt(lastIndex) + 1
				const type = this.newEquipment
				this.form.equipments[index] = { type, index }
				this.showAddEquipment = false
				this.form = Object.assign({}, this.form)
				this.changedValues()
			},
			deleteEquipment(index) {
				delete this.form.equipments[index]
				this.form = Object.assign({}, this.form)
				this.changedValues()
			},
			showPDF() {
				const url = window.location.toString().split('#')[0]
				window.open(url + '/#/budget-pdf/' + this.id)
			},
			finishBudget() {
				if (this.id && this.id != 0) {
					Budgets.finishBudget(this.form, (result) => {
						this.alert = MessageError.getMessage(this, result, 'title', 'budgets')
					})
				}
			},
		},
	}
</script>
